import React, { useRef, useEffect } from "react";

import { IconButton } from "@material-ui/core";
import { KeyboardArrowLeft, KeyboardArrowRight } from "@material-ui/icons";

import { QuizSliderProps } from "types/Types";

const QuizSlider = ({
    setCurrentQuiz,
    maxQuizNum,
    currentQuizNum,
}: QuizSliderProps) => {
    const leftButton = useRef<any>(null);
    const rightButton = useRef<any>(null);

    // í˜„ì¬ ë³´ì—¬ì§€ëŠ” Quiz ë²ˆí˜¸ë¥¼ ê¸°ì¤€ìœ¼ë¡œ ì¢Œì¸¡, ìš°ì¸¡ ë²„íŠ¼ì˜ ë³´ì—¬ì§ì„ classListë¥¼ ì‚¬ìš©í•˜ì—¬ ì„¤ì •
    useEffect(() => {
        leftButton.current.classList.remove("slider__hide");
        rightButton.current.classList.remove("slider__hide");

        // classListë¥¼ ìˆ˜ì •í•´ì•¼ í•  Buttonì„ í• ë‹¹ í›„ ìˆ˜ì •
        let modifingButton: React.MutableRefObject<any> | null = null;
        if (currentQuizNum === 0) {
            modifingButton = leftButton;
        } else if (currentQuizNum === maxQuizNum) {
            modifingButton = rightButton;
        }
        modifingButton?.current.classList.add("slider__hide");
    }, [currentQuizNum]);

    // í˜„ì¬ ìŠ¬ë¼ì´ë“œë¥¼ ê¸°ì¤€ìœ¼ë¡œ onClick ê¸°ëŠ¥ì„ ì˜ˆì™¸ì²˜ë¦¬
    const onClickLeft = () => {
        if (currentQuizNum > 0) {
            setCurrentQuiz((prev) => prev - 1);
        }
    };

    const onClickRight = () => {
        if (currentQuizNum < maxQuizNum) {
            setCurrentQuiz((prev) => prev + 1);
        }
    };

    return (
        <div className="quiz__slider">
            <IconButton
                ref={leftButton}
                onClick={onClickLeft}
                className={"slider__btn slider__hide"}
                color="primary"
                aria-label="left"
                component="span"
            >
                <KeyboardArrowLeft />
            </IconButton>

            <IconButton
                ref={rightButton}
                onClick={onClickRight}
                className="slider__btn"
                color="primary"
                aria-label="right"
                component="span"
            >
                <KeyboardArrowRight />
            </IconButton>
        </div>
    );
};

export default QuizSlider;
