import React, { useState, useEffect, useRef } from "react";
import { setTimeout } from "timers";

const Loading = () => {
    // loading ì™„ë£Œ ìƒíƒœë¥¼ í™•ì¸ í•  state
    const [isLoading, setIsLoading] = useState<boolean>(false);
    // loading domì„ ì—°ê²°í•  ref
    const Loader = useRef<any>();

    const LOADING_TIME: number = 1500;
    const ANIMATION_TIME: number = 850;

    useEffect(() => {
        // ì •í•´ì§„ ì‹œê°„ í›„ loading divì˜ class loading__endë¥¼ ì¶”ê°€í•˜ê¸° ìœ„í•´
        setTimeout(() => {
            setIsLoading(true);
        }, LOADING_TIME);

        // Loading divì˜ fade out animationì´ ì¢…ë¥˜ í›„ ì‚­ì œí•˜ê¸° ìœ„í•´
        setTimeout(() => {
            Loader.current.remove();
        }, LOADING_TIME + ANIMATION_TIME);
    }, []);

    return (
        <div
            ref={Loader}
            className={`loading ${isLoading ? "loading__end" : ""}`}
        >
            <div className="loading__circle"></div>
        </div>
    );
};

export default Loading;
