import React, { useState, useEffect } from "react";

import { QuizProgressProps } from "types/Types";

const QuizProgress = ({
    maxQuizNum,
    currentQuizNum,
    setCurrentQuizNum,
}: QuizProgressProps) => {
    const [progresses, setProgresses] = useState<string[]>();
    const PROGRESS_CN: string = "progress__element";
    const SELECTED_PROGRESS_CN: string = " progress__selected";

    // currentQuizNum stateê°€ ë°”ë€” ì‹œ progressì˜ í´ë˜ìŠ¤ ë¦¬ìŠ¤íŠ¸ë¥¼ ì„¤ì •
    useEffect(() => {
        // ëª¨ë‘ ë“¤ì–´ê°€ëŠ” í´ë˜ìŠ¤ëª…ì„ ì¶”ê°€í•˜ì—¬ ë°°ì—´ ìƒì„±
        const tempProgress: string[] = Array(maxQuizNum + 1).fill(PROGRESS_CN);

        // ë°˜ë³µë¬¸ì„ ìˆ˜í–‰í•˜ë©° í˜„ì¬ ìŠ¬ë¼ì´ë”ì— ë§ê²Œ í´ë˜ìŠ¤ëª…ì„ ì¶”ê°€
        for (let i = 0; i <= maxQuizNum; i++) {
            if (i === currentQuizNum) {
                tempProgress[i] += SELECTED_PROGRESS_CN;
            }
        }

        setProgresses(tempProgress);
    }, [maxQuizNum, currentQuizNum]);

    // í•˜ë‹¨ progress í´ë¦­ ì‹œ í•´ë‹¹ idê°€ ë³´ì—¬ì£¼ëŠ” slideë¡œ ë„˜ì–´ê°€ê¸° ìœ„í•´
    // currenQuizNumì„ setStating
    const onClick = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        const { id } = event.target as HTMLDivElement;
        setCurrentQuizNum(parseInt(id));
    };

    return (
        <div className="quiz__progress">
            <div className="progress__box">
                {progresses?.map((progress, index) => {
                    return (
                        <div
                            className={progress}
                            key={index}
                            id={`${index}`}
                            onClick={onClick}
                        ></div>
                    );
                })}
            </div>
        </div>
    );
};

export default QuizProgress;
