import React from "react";
import { Switch, Route, withRouter } from "react-router-dom";
import { TransitionGroup, CSSTransition } from "react-transition-group";

import Auth from "pages/Auth/Auth";
import Quiz from "pages/Quiz/Quiz";
import Waiting from "pages/Waiting/Waiting";
import About from "pages/About/About";

import useAuth from "hooks/useAuth";
import useCheckCorrectedUser from "hooks/useCheckCorrectedUser";

const TransitionRouter = withRouter(({ location }) => {
    const currentUser = useAuth();
    const isCorrectedUser = useCheckCorrectedUser(currentUser);

    return (
        <TransitionGroup className="app">
            <CSSTransition
                key={location.pathname}
                classNames="slide"
                timeout={1500}
            >
                <Switch location={location}>
                    {currentUser === null ? (
                        // ë¡œê·¸ì¸ ì•ˆí–ˆì„ ì‹œ
                        <Route exact path="/">
                            <Auth />
                        </Route>
                    ) : isCorrectedUser ? (
                        // ì •ë‹µì„ ë§ì¶˜ ìœ ì €ì¼ ì‹œ
                        <Route exact path="/">
                            <Waiting />
                        </Route>
                    ) : (
                        // ì •ë‹µì„ ë§ì¶”ê¸° ì „ì¸ ìœ ì €ì¼ ì‹œ
                        <Route exact path="/">
                            <Quiz />
                        </Route>
                    )}
                    <Route path="/about">
                        <About />
                    </Route>
                </Switch>
            </CSSTransition>
        </TransitionGroup>
    );
});

export default TransitionRouter;
