import React, { useEffect, useState } from "react";
import { Snackbar, Slide } from "@material-ui/core";
import { Alert } from "@material-ui/lab";
import { TransitionProps } from "@material-ui/core/transitions";

import { CustomAlertProps } from "types/Types";

const CustomAlert = ({ msg, setMsg }: CustomAlertProps) => {
    // ê²½ê³ ì°½ì˜ ì—´ë ¤ìˆìŒê³¼ transitionì„ ê´€ë¦¬í•˜ëŠ” state
    const [state, setState] = useState<{
        open: boolean;
        Transition: React.ComponentType<
            TransitionProps & { children?: React.ReactElement<any, any> }
        >;
    }>({
        open: false,
        Transition: Slide,
    });

    // ì•ŒëŒì˜ ìœ„ì¹˜ ê°’
    const vertical: any = "bottom";
    const horizontal: any = "right";

    // Propìœ¼ë¡œ ë„˜ì–´ì˜¨ ì—ëŸ¬ ë©”ì„¸ì§€ê°€ ë¹ˆ ë¬¸ìì—´ì´ ì•„ë‹ ì‹œ
    useEffect(() => {
        setState({
            ...state,
            open: msg !== "",
        });
    }, [msg]);

    // ë‹«ê¸° ë²„íŠ¼ì„ ëˆ„ë¥¼ ì‹œ
    const onClose = () => {
        setState({
            ...state,
            open: false,
        });
        setMsg("");
    };

    return (
        <Snackbar
            open={state.open}
            onClose={onClose}
            TransitionComponent={state.Transition}
            key={state.Transition.name}
            anchorOrigin={{ vertical, horizontal }}
        >
            <Alert onClose={onClose} severity="warning">
                {msg}
            </Alert>
        </Snackbar>
    );
};

export default CustomAlert;
