import React, { useState } from "react";
import { Checkbox } from "@material-ui/core";
import {
    Clear,
    ClearOutlined,
    PanoramaFishEye,
    PanoramaFishEyeOutlined,
} from "@material-ui/icons";

import QuizName from "components/Quiz/QuizName";
import { QuizSelectInterface } from "types/Types";

const QuizSelect = ({
    id,
    quiz,
    answer,
    setAnswer,
    isCurrent,
}: QuizSelectInterface) => {
    const [myAnswer, setMyAnswer] = useState<boolean>(true);

    // Propìœ¼ë¡œ ë°›ì–´ì˜¤ëŠ” answerì€ ì •ë‹µë“¤ì´ ëª¨ì—¬ìˆëŠ” ë°°ì—´
    // ì²´í¬ ë°•ìŠ¤ë¥¼ í´ë¦­ ì‹œ í˜„ì¬ ì„ íƒí•œ ê²ƒê³¼ ë°˜ëŒ€ì˜ ì •ë³´ë¥¼ í•´ë‹¹ answer stateì˜ setStating
    // myAnswer stateëŠ” í˜„ì¬ componentì˜ ê°’ì„ ìˆ˜ì •í•˜ê¸° ìœ„í•¨
    const onChange = () => {
        if (id || id === 0) {
            answer[id] = !answer[id];
            setAnswer?.(answer);
            setMyAnswer(answer[id]);
        }
    };

    return (
        <div className={`quiz__select ${isCurrent && "quiz__select__showing"}`}>
            <div className="select__button">
                <Checkbox
                    onChange={onChange}
                    checked={!myAnswer}
                    className="quiz__no"
                    icon={<ClearOutlined />}
                    checkedIcon={<Clear />}
                />

                <Checkbox
                    onChange={onChange}
                    checked={myAnswer}
                    className="quiz__yes"
                    icon={<PanoramaFishEyeOutlined />}
                    checkedIcon={<PanoramaFishEye />}
                />
            </div>

            <QuizName name={quiz?.name as string} index={id as number} />
        </div>
    );
};

export default QuizSelect;
